import Vue from 'vue'
import Router from 'vue-router'
// import HelloWorld from '@/components/HelloWorld'

Vue.use(Router)

// 导入组件
import home from '../components/index.vue'//首页
import git_use from '../components/detail/git_use.vue'//首页
import git_morekf from '../components/detail/git_morekf.vue'//首页

export default new Router({
  mode: 'history',
  routes: [
    {path: '/',redirect:'home',component: home},
    {path: '/home',name: 'home',component: home},
    {path: '/git_use',name: 'git_use',component: git_use},
    {path: '/git_morekf',name: 'git_morekf',component: git_morekf},
    
  ]
})



