<template>
    <div class="main">
        
        <div class="indexbanner">
            <div class="swiper-container index_ban">
                <div class="swiper-wrapper">
                    <div class="swiper-slide" v-for="item in bannerlist">
                        <div class="index_banimg"><img src="../assets/image/w1920h600.png" :style="{background:'url('+item+') no-repeat center center/cover'}" alt=""></div>
                    </div>
                    
                </div>
                <!-- <div class="swiper-pagination index_pagination"></div> -->
            </div>
        </div>
        <div class="container">
            <div class="main1 pz10">
                <ul>
                    <li v-for="item in artical_list">
                        <h4>
                            <!-- <a :href="item.url">{{item.name}}</a> -->
                            <router-link :to="{path:item.url}">{{item.name}}</router-link>
                        </h4>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>
<script>
// import myHeader from '../common/header.vue'
import Swiper from 'swiper';
import $ from 'jquery';
// console.log($('body').html());
export default {
    name:'index',
    data(){
        return{
            bannerlist:[
                '../../static/image/2.jpg',
                '../../static/image/5.jpg',
                '../../static/image/7.jpg'
            ],
            artical_list:[
                {
                    url: '/git_use',
                    name:'Git使用基本流程'
                },
                {
                    url: '/git_morekf',
                    name:'Git多人协同开发产生冲突问题'
                },
            ]
        }
    },
    created(){
        
    },
    mounted(){
        var swiper_bg = new Swiper ('.index_ban', {
            loop: true,
            lazy: true,//延迟加载图片
            // zoom: true,//缩放
            effect: 'fade',
            speed: 8000,
            autoplay: {
                delay: 8000,
                disableOnInteraction: false,
            },
            pagination: {
                el: '.index_pagination',
                clickable: true,
            },
        })
    },
    methods:{

    },
    components: {
        // myHeader:myHeader
    },
}
</script>
<style>
     @import'swiper/dist/css/swiper.min.css';
</style>


